import classes from "polythene-css-classes/icon-button";

export const _IconButton = ({ h, a, Icon, Button, ...props }) => {
  const content = props.content
    ? props.content
    : props.icon
      ? h(Icon, props.icon)
      : props.children;
  const componentProps = Object.assign(
    {},
    {
      content: h("div", { [a.className]: classes.content }, content),
      after: props.label && h("div", { [a.className]: classes.label }, props.label),
      parentClassName: [
        props.parentClassName || classes.component,
        props.compact ? classes.compact : null,
      ].join(" "),
      // defaults
      wash: false,
      animateOnTap: false
    },
    props
  );

  return h(Button, componentProps);
};
